
.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
    height: 650px;
    width: 100%;
    margin-bottom: 0px;
    /* border:2px solid black; */
}

.popular h1{
    padding: 0;
    margin:0;
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.popular-item{
    margin-top:50px;
    display:flex;
    gap:30px;
}

@media only screen and (max-width:800px) {
    .popular h1{
        font-size: 30px;
        font-weight: 600;
    }
}