.product_image {
	height: auto;
	width: 150px;
}

.productdisplay {
	display: flex;
	margin: 0px 80px;
}

.productdisplay_left {
	display: flex;
	gap: 17px;
}
 
.productdisplay_img_list {
	display: flex;
	flex-direction: column;
	gap: 15px;
}

.productdisplay_img_list img {
	height: 100px;
	width: auto;
}


.productdisplay_main_img {
	height: 445px;
	width: auto;

}

.productdisplay_right {
	margin: 0px 70px;
	display: flex;
	flex-direction: column;
}

.productdisplay_right h1 {
	margin: 0;
	color: #3d3d3d;
	font-size: 40px;
	font-weight: 700;
}

.productdisplay_right_stars {
	display: flex;
	align-items: center;
	margin-top: 13px;
	gap: 5px;
	color: #1c1c1c;
	font-size: 16px;
}

.productdisplay_right_prices {
	display: flex;
	margin: 10px 0px;
	gap: 30px;
	font-size: 24px; 
	font-weight: 700;

}

.productdisplay_right_price_old {
	color: #818181;
	text-decoration: line-through;
}

.productdisplay_right_price_new {
	color: #ff4141;
}

.productdisplay_right_size h1 {
	margin-top: 55px;
	color: #656565;
	font-size: 20px;
	font-weight: 600;
}

.productdisplay_right_sizes {
	display: flex;
	margin: 30px 0;
	gap: 20px;
}
/* .productdisplay_right_size{
} */

.productdisplay_right_size div {
	text-align: center;
	padding: 15px 20px;
	background: #fbfbfb;
	border: 1px solid #ebebeb;
	border-radius: 3px;
	cursor: pointer;
	width: 90%;

}

.productdisplay_right button {
	padding: 20px 40px;
	width: 200px;
	font-size: 16px;
	font-weight: 600;
	color: white;
	background: #ff4141;
	margin-bottom: 40px;
	border: none;
	outline: none;
	cursor: pointer;
}

.productdisplay_right_category {
	margin-top: 10px;
}

.productdisplay_right_category span {
	font-weight: 600;
}
